"use client";

import { useState } from "react";
import type { Grupo } from "../lib/api";
import { clientApi } from "../lib/client";
import { useRouter } from "next/navigation";

type Props = {
  grupo: Grupo;
  onClose: () => void;
};

export default function EditGroupModal({ grupo, onClose }: Props) {
  const router = useRouter();
  const [status, setStatus] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  async function handleSubmit(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    setLoading(true);
    const form = new FormData(event.currentTarget);
    try {
      await clientApi.put(`/grupos/${grupo.id}`, {
        group_id: form.get("group_id"),
        nome: form.get("nome_grupo"),
        enviar_mensagem: form.get("envio") === "on",
        cr: form.get("cr")
      });
      setStatus("Grupo atualizado com sucesso!");
      router.refresh();
      setTimeout(onClose, 1200);
    } catch (error: any) {
      setStatus(error.message ?? "Erro ao atualizar o grupo.");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="modal-backdrop">
      <div className="modal">
        <div className="modal-header">
          <h3>Editar {grupo.nome_grupo}</h3>
          <button type="button" className="secondary" onClick={onClose}>
            Fechar
          </button>
        </div>
        <form className="grid" style={{ gap: "1rem" }} onSubmit={handleSubmit}>
          <label>
            Group ID
            <input name="group_id" defaultValue={grupo.group_id} required />
          </label>
          <label>
            Nome
            <input name="nome_grupo" defaultValue={grupo.nome_grupo} required />
          </label>
          <label>
            CR
            <input name="cr" defaultValue={grupo.cr ?? ""} />
          </label>
          <label className="checkbox-container">
            <input type="checkbox" name="envio" defaultChecked={grupo.envio} />
            <span>Habilitar envio</span>
          </label>
          <div style={{ display: "flex", justifyContent: "flex-end", gap: "0.75rem" }}>
            <button type="submit" disabled={loading}>
              {loading ? "Salvando..." : "Salvar altera√ß√µes"}
            </button>
          </div>
        </form>
        {status && <p style={{ marginTop: "0.5rem" }}>{status}</p>}
      </div>
    </div>
  );
}
